@import "./components/components.fonts";
@import "./components/components.mixins";
@import "./components/components.general";
@import "./components/components.header";
@import "./components/components.footer";

* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-collapse: collapse;
}

img {
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none !important;
  -webkit-user-select: none !important;
}

a {
  color: inherit;
  cursor: pointer;
}
a:focus {
  outline: none;
}
button {
  cursor: pointer;
  border: none;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  // outline: 1px solid red;
}

body {
  font-family: $font;
  font-size: 18px;
  line-height: 120%;
  color: #000000;
  font-weight: 400;
}

.overflow {
  position: relative;
  overflow: hidden;
}
input,
select,
textarea,
a,
button {
  font-family: $font;
}

select {
  font-family: inherit, sans-serif !important;
}

//НАЧАЛО - СЕКЦИЯ offer *****************************************************

.offer {
  padding: 0px 30px 0px 30px;
}
.offer__container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-end;
  position: relative;
}
.offer__left {
  width: 62%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.offer__right {
  width: 35%;
}
.offer__photo1 {
  margin-left: -60px;
}
.offer__title {
  width: 100%;
  max-width: 535px;
  font-weight: 600;
  font-size: 48px;
  line-height: 120%;
  text-transform: uppercase;
  color: #000000;
  text-align: right;
  margin-top: 30px;
  margin-bottom: 60px;
  span {
    font-size: 96px;
    font-weight: 800;
  }
}
.btn-order {
  width: 300px;
  height: 90px;
  padding: 20px;
  color: white;
  font-weight: 300;
  font-size: 30px;
  text-transform: uppercase;
  background: black;
