  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background: url(../img/arrow-right_white.png) 90% 50% no-repeat, black;
  transition: all ease-out 0.3s;
}
.btn-order:hover {
  background: url(../img/arrow-right_black.png) 90% 50% no-repeat, white;
  border: 1px solid #000000;
