.form__size-wrapper {
  margin-bottom: 70px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
}
.checkbox__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.size-checkbox {
  background: #fee2d9;
  width: 40px;
  height: 40px;
  position: relative;
}
.size-label {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid black;
  margin-left: -77px;
  position: relative;
  z-index: 5;
  background: #fee2d9;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-size: 14px;
  line-height: 140%;
  cursor: pointer;
}
.size-label:before {
  content: "";
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0px;
  top: 0px;
  color: rgba(0, 0, 0, 0.3);
  z-index: 15;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.size-label:after {
  content: "";
  display: block;
  position: absolute;
  left: -16px;
  bottom: -35px;
  color: black;
  z-index: 15;
  transform: rotate(-90deg);
  width: 70px;
  opacity: 0;
  transition: all ease-out 0.3s;
  pointer-events: none;
}
.size36:after {
  content: "24 см";
}
.size37:after {
  content: "24,5 см";
}
.size38:after {
  content: "25 см";
}
.size39:after {
  content: "25,5 см";
}
.size40:after {
  content: "26 см";
}

.product2 .size37:after {
  content: "24 см";
}
.product2 .size38:after {
  content: "24,5 см";
}
.product2 .size39:after {
  content: "25 см";
}
.product2 .size40:after {
  content: "25,5 см";
}
.product2 .size41:after {
  content: "26 см";
}

.product3 .size36:after {
  content: "23,5 см";
}
.product3 .size37:after {
  content: "24 см";
}
.product3 .size38:after {
  content: "24,5 см";
}
.product3 .size39:after {
  content: "25 см";
}
.product3 .size40:after {
  content: "25,5 см";
}

.label_active:after {
  opacity: 1;
}
.product__price {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.product__price-new {
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  text-transform: uppercase;
  color: #dd9a00;
}
.product__price-old {
