    width: 100%;
  }
}
.btn-slider {
  cursor: pointer;
  transition: all ease-out 0.3s;
}
.btn-slider:hover {
  transform: scale(1.05);
}
.product__prev {
  position: absolute;
  left: 45%;
  bottom: -80px;
}
.product__next {
  position: absolute;
  left: 70%;
  bottom: -80px;
}
.product__bg {
