//НАЧАЛО - СЕКЦИЯ media offer *****************************************************

@media (max-width: 1280px) {
  .offer__photo1 {
    margin-left: -73px;
  }
  .offer__abs-name {
    left: 169px;
  }
}

@media (max-width: 1024px) {
  .offer .btn-order {
    position: relative;
    z-index: 10;
  }
  .offer__abs-figure {
    transform: rotate(15deg);
  }
  .offer__abs-text {
    display: none;
  }
}

@media (max-width: 950px) {
  .offer__wrapper {
    flex-wrap: wrap;
  }
  .offer__title {
    position: absolute;
    right: 0;
    top: 0;
  }
  .offer__wrapper {
    margin-top: 190px;
  }
  .offer__abs-year {
    left: auto;
    top: 170px;
    right: 0;
  }
